.container {
	--border-radius: 10px;
	--color-light: #29e52d;
	--color-dark: #d82228;
	--color-neitral: #ffff00;

	--color-shadow-light: rgba(41, 229, 45, 0.4);
	--color-shadow-dark: rgba(216, 34, 40, 0.4);
	--color-shadow-neitral: rgba(224, 224, 81, 0.4);
}

.container {
	display: flex;
	flex-wrap: wrap;
	margin-top: 40px;
}

.item {
	position: relative;
	top: 0;
	left: 0;
	width: 250px;
	height: 450px;
	margin-right: 20px;
	border-radius: var(--border-radius);
	box-shadow: var(--box-shadow-black);
	transition: 0.4s;
	cursor: pointer;
}

.item__header {
	position: absolute;
	bottom: 20px;
	width: 100%;
	font-size: 18px;
	text-align: center;
	z-index: 1;
}

.item__img {
	width: 100%;
	height: 100%;
	object-fit: cover;
	object-position: center center;
	border-radius: var(--border-radius);
}

.item__dark {
	text-shadow: 0 0 5px currentColor;
	color: var(--color-dark);
}

.item__light {
	text-shadow: 0 0 5px currentColor;
	color: var(--color-light);
}

.item__neitral {
	text-shadow: 0 0 5px currentColor;
	color: var(--color-neitral);
}

.item__light:hover {
	box-shadow: 0 0 7px 2px var(--color-shadow-light);
}

.item__neitral:hover {
	box-shadow: 0 0 7px 2px var(--color-shadow-neitral);
}

.item__dark:hover {
	box-shadow: 0 0 7px 2px var(--color-shadow-dark);
}
