.wrapper {
	width: 300px;
	padding: 20px;
	margin-left: 30px;
	background-color: rgba(0, 0, 0, 0.65);
	border-radius: 5px;
	color: #fff;
}

.list__container {
	list-style-type: none;
	margin: 0;
	padding: 0;
}

.list__item {
	padding: 5px;
	margin-bottom: 7px;
}

.item__title {
	text-decoration: underline;
	text-shadow: var(--text-shadow-blue);
}

@media screen and (max-width: 800px) {
	.wrapper {
		width: 100%;
		margin: 0;
		margin-top: 20px;
	}
}
